@mixin ld {
	@media only screen and (max-width: "1649px") {
		@content; } }
@mixin lg {
	@media only screen and (max-width: "1499px") {
		@content; } }
@mixin dm {
	@media only screen and (max-width: "1299px") {
		@content; } }
@mixin dt {
	@media only screen and (max-width: "1199px") {
		@content; } }
@mixin ds {
	@media only screen and (max-width: "1023px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }
@mixin xs {
	@media only screen and (max-width: "767px") {
		@content; } }

$white: #fff;
$blue: #325578;
$blue-d: #0c437b;
$blue-bg: #001326;
$blue-l: #f3f7fb;
$orange: #fc6001;
$red: #fc1f01;
$red2-d: #d10b02;
$black: #000;
$sd: 1.5px 2.6px 17.1px 0.9px rgba(26, 71, 117, 0.41);
$sdb: 5px 8.7px 10px rgba(12, 67, 123, 0.5);

body {
	font-family: 'Roboto Condensed', sans-serif;
	font-weight: 400;
	line-height: 1.2;
	font-size: 20px;
	@include lg {
		font-size: 16px; } }

h1,h2,h3,h4,h5,h6 {
	font-weight: 400; }
a {
	text-decoration: none; }

.title-lg {
	color: $white;
	font-size: 44px;
	font-weight: 700;
	line-height: 1;
	@include lg {
		font-size: 33px; }
	@include dt {
		font-size: 28px; }
	@include m {
		font-size: 24px; }
	&--blue {
		color: $blue-d; } }
.title-m {
	color: $white;
	font-size: 44px;
	font-weight: 700;
	line-height: 1;
	@include lg {
		font-size: 34px; }
	@include m {
		font-size: 22px; }
	&--blue {
		color: $blue-d; } }
.title-s {
	color: $white;
	font-size: 30px;
	font-weight: 700;
	line-height: 1.3;
	@include lg {
		font-size: 22px; }
	@include dt {
		font-size: 20px; } }
.logo {
	display: block;
	img {
		width: 191px;
		height: 65px; }
	@include ld {
		img {
			width: 146px;
			height: 50px; } }
	@include dt {
		img {
			width: 100px;
			height: 34px; } } }

.nav {
	font-size: 0;
	text-align: center;
	&__item {
		display: inline-block;
		vertical-align: top;
		&.active {
			.nav__link {
				color: $white;
				background: $blue-d; } }
		&:hover {
			.nav__link {
				color: $white;
				background: $blue-d; } } }
	&__link {
		display: block;
		padding: 5px 15px 15px 15px;
		@include trans;
		font-size: 28px;
		line-height: 1;
		color: $blue;
		@include ld {
			font-size: 24px; }
		@include lg {
			font-size: 18px; }
		@include dt {
			font-size: 16px;
			padding: 5px 12px 11px 12px; } } }
.loc {
	font-size: 0;
	display: inline-block;
	vertical-align: top;
	margin-top: 8px;
	@include dt {
			margin-top: 2px; }
	&__icon {
		color: $orange;
		font-size: 19px;
		font-weight: 300;
		width: 26px;
		height: 26px;
		line-height: 26px;
		text-align: center;
		border-radius: 50%;
		border: 1px solid $orange;
		display: inline-block;
		vertical-align: top;
		margin-right: 10px;

		@include ld {
			width: 21px;
			height: 21px;
			line-height: 21px;
			font-size: 15px; } }
	span {
		color: $blue;
		font-size: 24px;
		display: inline-block;
		vertical-align: top;
		line-height: 1.1;
		@include ld {
			font-size: 18px; }
		@include dt {
			font-size: 16px; }
		@include dt {
			margin-top: 2px; } } }
.phone {
	color: $blue;
	font-size: 36px;
	font-weight: 400;
	span {
		font-weight: 700; }
	@include ld {
		font-size: 28px; }
	@include dt {
		font-size: 20px; }
	svg {
		display: none;
		fill: $orange;
		@include dt {
			display: inline-block;
			position: relative;
			top: 5px; } } }
.btn {
	min-width: 294px;
	height: 51px;
	background-color: $orange;
	border-radius: 10px;
	text-align: center;
	border: 1px solid $orange;
	position: relative;
	@include ld {
		min-width: 230px;
		height: 40px; }
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		border-radius: 10px;
		background: $white;
		opacity: 0;
		@include trans; }
	span {
		position: relative;
		z-index: 1;
		color: $white;
		font-size: 24px;
		line-height: 51px;
		@include trans;
		@include ld {
			line-height: 40px;
			font-size: 18px; } }
	&:hover {
		&:after {
			opacity: 1; }
		span {
			color: $orange; } } }
