.header {
	padding-top: 6px;
	&__wrapper {
		position: relative;
		@include clr; }
	&__left {
		float: left; }
	&__nav {
		max-width: 1270/1840*100 + %;
		//position: absolute
		padding-top: 20px;
		//left: 40.7%
		//transform: translateX(-50%)
		@include ld {
			padding-top: 8px; }
		@include lg {
			padding-top: 15px; }
		@include dt {
			padding-top: 9px;
 } }			//left: 39.7%
	&__right {
		position: absolute;
		top: 20px;
		right: 0;
		@include ld {
			top: 12px; }
		@include dt {
			top: 9px; } }
	&__loc {
		// margin-top: 8px
		margin-right: 46px;
		@include ld {
			margin-right: 40px; }
		@include dt {
			margin-right: 20px; }
		@include ds {
			margin-right: 10px; }
		//+dt
 } }		//	margin-top: 2px
.ndropdown {
	z-index: 2;
	position: absolute;
	padding: 18px 30px;
	top: 105%;
	box-shadow: $sdb;
	width: 1530px;
	left: 0;
	font-size: 0;
	@include ld {
		width: 1200px; }
	&__wrapper {
		@include clr; }
	&__left {
		width: 875/1470*100 + %;
		float: left; }
	&__right {
		width: 545/1470*100 + %;
		float: right;
		position: relative;
		@include cover;
		height: 485px;
		@include ld {
			height: 378px; }
		svg {
			fill: $blue;
			font-size: 20px;
			position: absolute;
			left: -55px;
			top: 43%;
			margin-top: -10px;
			@include ld {
				font-size: 16px;
				left: -42px;
				margin-top: -8px; } } }
	&__btn {
		position: absolute;
		bottom: 35px;
		left: 50%;
		transform: translateX(-50%); } }
.dropitem {
	width: 25%;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	margin-bottom: 25px;
	&--last-row {
		margin-bottom: 0; }
	&__img {
		width: 184px;
		height: 169px;
		margin: 0 auto 5px;
		img {
			object-fit: cover;
			height: 100%;
			width: 100%; }
		@include ld {
			width: 144px;
			height: 136px; } }
	&__text {
		color: $blue;
		font-size: 24px;
		line-height: 1.3;
		@include ld {
			font-size: 18px; } } }
.discount {
	position: relative;
	background: $blue-d;
	width: 425px;
	margin-top: 38px;
	padding: 18px 18px 29px 18px;
	@include ld {
		width: 300px;
		padding: 12px 10px 25px 10px; }
	&:before {
		content: '';
		position: absolute;
		width: 86px;
		height: 86px;
		border-right: 9px solid  $orange;
		border-bottom: 9px solid  $orange;
		bottom: -9px;
		right: -9px;
		@include ld {
			width: 68px;
			height: 68px;
			border-right: 7px solid  $orange;
			border-bottom: 7px solid  $orange;
			bottom: -7px;
			right: -7px; } }
	&__sec-text, &__main-text {
		color: $white; }
	&__main-text {
		font-weight: 700;
		font-size: 30px;
		@include ld {
			font-size: 24px; }
		span {
			font-size: 36px;
			@include ld {
				font-size: 28px; } } }
	&__sec-text {
		font-size: 24px;
		font-weight: 400;
		@include ld {
			font-size: 18px; } } }
